<template>
  <div class="container">
    <van-cell
      v-for="(item, index) in list"
      :key="index"
      @click="onClick(item.id)"
    >
      {{ index + 1 }}、{{ item.title }}
    </van-cell>
  </div>
</template>
<script>
export default {
  data() {
    return {
      list: [],
    };
  },
  created() {
    this.list = require("./list.json");
  },
  methods: {
    onClick(id) {
      this.$router.push({
        path: "/writing/detail",
        query: {
          id,
        },
      });
    },
  },
};
</script>
<style lang="scss" scoped></style>
